import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewDefect from '@/components/NewDefect'
import ViewDefect from '@/components/ViewDefect'
import EditDefect from '@/components/EditDefect'
import Login from '@/components/Login'
import Register from '@/components/Register'
import firebase from 'firebase'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/new',
      name: 'new-defect',
      component: NewDefect,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/edit/:reg',
      name: 'edit-defect',
      component: EditDefect,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/:reg',
      name: 'view-defect',
      component: ViewDefect,
      meta: {
        requiresAuth: true
      }
    }
  ]
})

//Nav Gaurds

router.beforeEach((to, from, next) => {
  // check for requiredAuth gaurd
  if(to.matched.some(record => record.meta.requiresAuth)) {
    // check if NOT logged in
    if(!firebase.auth().currentUser) {
      // Go to login
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      })
    } else {
      // procceed to route
      next()
    }
  } else {
    if(to.matched.some(record => record.meta.requiresGuest)) {
      // check if logged in
    if(firebase.auth().currentUser) {
      // Go to login
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      })
    } else {
      // procceed to route
      next()
    }
    } else {
      // procceed to route
      next()
    }
  } 
})

export default router



