<template>
<div class="container">
  <div id="new-defect">
      <h3>New Defect Record</h3>
      <div class="row">
          <form @submit.prevent="saveDefect" class="col s12">
              <div class="row">
                  <div class="input-field col s12">
                      <input type="text" v-model="reg" required>
                      <label for="reg">Registration</label>
                  </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <input type="text" v-model="description" required>
                      <label for="description">Description</label>
                  </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <input type="date" v-model="date_recieved" required>
                      <label for="date_recieved">Date Recieved</label>
                  </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <input type="text" v-model="comments" >
                      <label for="comments">Comments</label>
                  </div>
              </div>
              <div class="row">
                  <div class="input-field col s12">
                      <input type="text" v-model="belongs_to" required>
                      <label for="belongs_to">Belongs to</label>
                  </div>
              </div>
              <button type="submit" class="btn">Submit</button>
              <router-link to="/" class="btn grey">Back</router-link>
          </form>
      </div>
  </div>
</div>    
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'
import moment from 'moment'

export default {
  name: 'new-defect',
  data () {
      return {
        reg: null,
        description: null,
        date_recieved: null,
        date_rectified: null,
        comments: null, 
        belongs_to: null,
        currentUser: false,
        created_at: null,
        created_by: null,
        moment: moment
      }
      
  },
    created () {
       if(firebase.auth().currentUser) {
           this.currantUser = firebase.auth().currentUser.email
       }
    },
  methods: {
     saveDefect ()  {
         db.collection('defects').add({
             reg: this.reg,
             description: this.description,
             date_recieved: this.date_recieved,
             comments: this.comments,
             belongs_to: this.belongs_to,
             created_at: moment(String(Date())).format('YYYY-MM-DD hh:mm a'),
             created_by: firebase.auth().currentUser.email
         })
         .then(docRef => {this.$router.push('/')})
         .catch(error => confirm.log(error))
     }
  }
  
}
</script>




