<template>
  <nav>
      <div class="nav-wrapper brown darken-4">
          <div class="container">
              <router-link to="/"
              class="brand-logo">Defect Report</router-link>
              <ul class="right">
                  <li v-if="isLoggedIn"><span class="white-text">{{currantUser}}</span></li>
                  <li v-if="isLoggedIn"><router-link to="/">Dashboard</router-link></li>
                  <li v-if="!isLoggedIn"><router-link to="/login">Login</router-link></li>
                  <li v-if="!isLoggedIn"><router-link to="/register">Register</router-link></li>
                  <li v-if="isLoggedIn"><button v-on:click="logOut" class="waves-effect waves-light btn black">LogOut</button></li>
              </ul>
          </div>
      </div>
  </nav>
</template>

<script>
import firebase from 'firebase'
export default {
    name: 'navbar',
    data () {
        return {
            isLoggedIn: false,
            currantUser: false
        }
    },
    created () {
       if(firebase.auth().currentUser) {
           this.isLoggedIn = true,
           this.currantUser = firebase.auth().currentUser.email
       }
    },
    methods: {
        logOut () {
            firebase.auth().signOut().then(() => {
                this.$router.go({path: this.$router.path})
            })
        }
    }
}
</script>



